*, :before, :after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  tab-size: 4;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --color-background: #84a178;
  --color-primary-text: #fcfcfc;
  --color-secondary-text: #131313;
  --color-text-orange: #d6936d;
  --color-btn-default: #f8d24e;
  --color-btn-hover_focus: #0a8817;
  --color-btn-disable: #ababab;
  --color-mobile-menu: #85a279;
  --color-btn-slider: #d9d9d9;
  --color-bg-modal: rgba(156, 190, 143, .7);
  --tint-icecream: #9cbe8f;
  --tint-icecoffee: #efd478;
  --tint-milkshake: #d6936d;
  --transition-duration-function: 250ms cubic-bezier(.4, 0, .2, 1);
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

html {
  scroll-behavior: smooth;
}

body {
  background: var(--color-background);
  letter-spacing: .04em;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
}

a:link, a:visited {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.backdrop {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, .2);
  position: fixed;
  top: 0;
  left: 0;
  transform: scale(1);
}

.container {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.btn {
  text-align: center;
  height: 42px;
  background-color: var(--color-btn-default);
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.page-header_horisontal {
  padding-top: 16px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .page-header_horisontal {
    gap: 32px;
    padding-top: 24px;
  }
}

@media screen and (min-width: 1200px) {
  .page-header_horisontal {
    justify-content: space-between;
    padding-top: 8px;
  }
}

.menu-open-btn {
  background-color: var(--color-background);
  cursor: url("cursor.e4f2bfcc.png"), auto;
  border: none;
  margin-left: auto;
  line-height: 0;
}

@media screen and (min-width: 1200px) {
  .menu-open-btn {
    display: none;
  }
}

.page-navigation {
  display: none;
}

@media screen and (min-width: 1200px) {
  .page-navigation {
    padding-top: 32px;
    display: flex;
  }
}

.btn-buy-now_mobile-hidden {
  display: none;
}

@media screen and (min-width: 768px) {
  .btn-buy-now_mobile-hidden {
    display: flex;
  }
}

.btn-buy-now_mobile-visible {
  display: flex;
}

@media screen and (min-width: 768px) {
  .btn-buy-now_mobile-visible {
    display: none;
  }
}

.mobile-menu {
  width: 100vw;
  height: 100%;
  z-index: 99;
  background-color: var(--color-background);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: transform .75s cubic-bezier(.4, 0, .2, 1), opacity .75s cubic-bezier(.4, 0, .2, 1), visibility .75s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  transform: translateX(100%);
}

.mobile-menu.is-open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateX(0);
}

.mobile-close-btn {
  cursor: url("cursor.e4f2bfcc.png"), auto;
  width: 32px;
  height: 32px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  padding: 0;
  line-height: 0;
  position: absolute;
  top: 16px;
  right: 20px;
}

.mobile-close-icon {
  color: var(--color-primary-text);
  fill: currentColor;
}

.mobile-menu-container {
  height: 100%;
  flex-direction: column;
  padding-top: 12px;
  padding-left: 31px;
  display: flex;
  position: relative;
}

.mobile-navigation {
  flex-direction: column;
  align-items: center;
  gap: 32px;
  margin-top: 51px;
  margin-bottom: 111px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .mobile-navigation {
    gap: 40px;
    margin-top: 76px;
  }
}

.mobile-navigation__link {
  color: var(--color-primary-text);
  font-size: 32px;
  font-weight: 700;
  line-height: 1.21875;
}

@media screen and (max-width: 319px) {
  .mobile-navigation__link {
    font-size: 10vw;
  }
}

@media screen and (min-width: 768px) {
  .mobile-navigation__link {
    font-size: 48px;
    font-weight: 700;
    line-height: 1.22917;
  }
}

.mobile-navigation__link:hover, .mobile-navigation__link:focus {
  color: var(--color-secondary-text);
}

.btn-buy-now {
  background-color: var(--color-btn-default);
  cursor: url("cursor.e4f2bfcc.png"), auto;
  border: none;
  border-radius: 10px;
  justify-content: center;
