.container-hero {
  position: relative;
}

.container-hero__title {
  text-transform: uppercase;
  color: var(--color-primary-text);
  font-family: Seymour One;
  font-style: normal;
  font-weight: 400;
}

.btn-products, .btn-made, .container-hero__arrow-btn {
  cursor: url("cursor.e4f2bfcc.png"), auto;
}

.btn-products {
  color: var(--color-secondary-text);
  background: none;
  border: 2px solid #f8d24e;
  font-weight: 600;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-products:hover {
  border: 2px solid var(--color-btn-hover_focus);
}

.btn-products:focus {
  border: 2px solid var(--color-btn-hover_focus);
  color: var(--color-primary-text);
}

.btn-products:disabled {
  border: 2px solid var(--color-btn-disable);
}

.btn-made {
  color: var(--color-btn-default);
  color: var(--color-btn-default);
  cursor: url("cursor.e4f2bfcc.png"), auto;
  background: none;
  border: none;
  font-weight: 600;
  text-decoration: underline;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
}

.btn-made:focus, .btn-made:hover {
  color: var(--color-btn-hover_focus);
}

.btn-made:focus {
  color: var(--color-primary-text);
}

.btn-made:disabled {
  color: var(--color-btn-disable);
}

.container-hero__description, .container-hero__image, .container-hero__item, .container-hero__arrow-btn {
  position: absolute;
  bottom: 0;
}

.container-hero__arrow-btn {
  fill: #fcfcfc;
  z-index: 70;
  cursor: url("cursor.e4f2bfcc.png"), auto;
  background: none;
  border: none;
  padding: 0;
}

.container-hero__arrow-icon {
  width: 40px;
  height: 40px;
}

.container-hero__arrow-btn:hover .container-hero__arrow-icon {
  width: 50px;
}

.container-hero__arrow-btn:focus .container-hero__arrow-icon {
  width: 60px;
}

